@import url(https://fonts.googleapis.com/css?family=Exo);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    opacity: .9;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.18), 0 2px 7px 0 rgba(0,0,0,.15);
    max-height: 10vh;  
}

.navbar img {
    max-height: 8vh;
}

.nav-item:hover {
    color: rgb(221,19,123);
} 

.active {
    color: rgb(0,109,165);
}

.foot {
    background-color: rgb(0,109,165);
    color: #fff;    
}

.foot li, .foot button {
    background-color: rgb(0,109,165);
}

.foot li {
    text-align: left;
}

.foot li a {
    color: #fff;;
}

.foot img {
    display: flex;
    align-items: flex-start;
    max-height: 8vh;
}

.clause {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    text-align: justify;  
    padding: 1em; 
}

.clause-btn,
.clause-btn:focus,
.clause-btn:visited {
    border: none;
    box-shadow: none; 
    color: #fff; 
    display: flex;
    justify-content: center;
}

.foot-add {
    display: flex;
    justify-content: center;
}

.fade {
    display: none;
}
.show {
    display: block;
}
.home-row {
    height: 100vh;
    margin: 3% auto;
    top: 30vh;
}

.home-row:first-of-type {
    margin-top: 1vh;
}

.carousel-item img {
    height: 100vh;
    width: 100vw;    
}

.carousel-item h3, 
.carousel-item p {
    text-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}

.home-col {
    padding: 0;
    width: 100%;
}

.qAndA-header {
    display: flex;
    flex-direction: row;
    align-items: center;    
}

.qAndA-header h5 {
    margin-bottom: 0;
}

.qAndA-open-btn {
    font-weight: 600;
}
.qAndA-text {
    text-align: justify;
    margin-left: 5%;
}
.item-qAndA {
    display: flex;
    justify-content: start;
}

.row-qAndA {
    margin: 40vh 0 5vh 0;    
}
.head-career {
    margin: 30vh 0 30vh 0;
}
.head-loan {
    margin: 20vh 0 5vh 0;
}
.contact-form-clause {
    display: flex;
    align-items: flex-start;
}

.contact-card-text {
    text-align: left;
}

.contact-card-text span {
    display: block;
}

.card {
    display: flex;
    align-items: center;
}
.content-policy {
    margin: 20vh;
    text-align: justify;
}
.content-cookies {
    margin: 20vh;
    text-align: justify;
}
.content {
    margin: 0;
}
.App {
  text-align: center;
  font-family: 'Exo', Tahoma, sans-serif;
}

li {
  list-style: none;
}

.container-fluid {
  padding: 0;
}




